import React, { useState } from "react";
import MainContainer from "../Container/MainContainer";
import usePlanets from "../../../query/planets/usePlanets";
import { Button, Input } from "@mui/material";
import { useDebounce } from "../../../hooks/useDebounce";

const Home = () => {
  const [searchTerm, setSearchTerm] = useState("");
  const debouncedSearchTerm = useDebounce(searchTerm, 1500);

  const { planetsResponse, isLoading, error, fetchPage, currentPage } =
    usePlanets({ search: debouncedSearchTerm });

  console.log(planetsResponse);
  return (
    <MainContainer>
      <Input
        placeholder="Search"
        value={searchTerm}
        onChange={(e: React.ChangeEvent<HTMLInputElement>) =>
          setSearchTerm(e.target.value)
        }
      />
      <Button onClick={() => fetchPage(currentPage + 1)}>Next</Button>
      <Button onClick={() => fetchPage(currentPage - 1)}>Previous</Button>
    </MainContainer>
  );
};

export default Home;
